<template>
  <div class="transaction-detail">
    <div class="detail-c">
      <ul class="nav-item clr">
        <li 
        class="nav-list" 
        :class="{'active': isActive == 0}"
        @click="changeActive(0)">{{$t('french.transactionDetail[0]')}}
          <span class="line" :class="{'line-active': isActive == 0}"></span>
        </li>
        <li 
        class="nav-list" 
        :class="{'active': isActive == 1}"
        @click="changeActive(1)">{{$t('french.transactionDetail[1]')}}
          <span class="line" :class="{'line-active': isActive == 1}"></span>
        </li>
      </ul>
      <template>
        <el-table
          :data="tableData"
          v-loading="tableLoading"
          style="width: 100%">
          <el-table-column
            prop="order_num"
            :label="$t('french.transactionDetail[2]')"
            width="220">
          </el-table-column>
          <el-table-column
            :label="$t('french.transactionDetail[3]')"
            width="220">
            <template slot-scope="scope">
              {{ $public.timestampToTimeshian(scope.row.add_time) }}
            </template>
          </el-table-column>
          <el-table-column
            prop="price"
            :label="$t('french.transactionDetail[4]')">
          </el-table-column>
          <el-table-column
            prop="total_num"
            :label="$t('french.transactionDetail[5]')"
            width="180">
          </el-table-column>
          <el-table-column
            prop="total_price"
            :label="$t('french.transactionDetail[6]')"
            width="180">
          </el-table-column>
          <el-table-column
            :label="$t('french.transactionDetail[7]')">
            <template slot-scope="scope">
              <span class="order-status" v-if="scope.row.status == 1">{{$t('french.transactionDetail[8]')}}</span>
              <span class="order-status" v-if="scope.row.status == 2">{{$t('french.transactionDetail[9]')}}</span>
              <span class="order-status" v-if="scope.row.status == 3" style="color: green;">{{$t('french.transactionDetail[10]')}}</span>
              <span class="order-status" v-if="scope.row.status == 4">{{$t('french.transactionDetail[11]')}}</span>
              <span class="order-status" v-if="scope.row.status == 5" style="color: red;">{{$t('french.transactionDetail[12]')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            :label="$t('french.transactionDetail[13]')">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" @click="goOrderDetail(scope.row.order_num)">{{$t('french.transactionDetail[14]')}}</el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination :page="page" @change="pageChange"></pagination>
      </template>
    </div>
  </div>
</template>

<script>
import  pagination from "@/components/pagination";
import { getPmmaList, getSellList } from '../../../api/c2c'
export default {
  name: 'transactionDetail',
  data() {
    return {
      page:{
          size: 10,
          p: 1,
          total: 10
      },
      isActive: 0,
      tableData: [],
      type: 'pmma',
      tableLoading: false
    }
  },
  created() {
    // 获取初始购买数据
    this.getTabelData()
  },
  methods: {
    pageChange(row){
        this.page.p = row
        if (this.isActive == 0) { // 购买列表
          this.getTabelData()
        } else if (this.isActive == 1) {//出售列表
          this.getSellData()
        }
    },
    // 查看详情
    goOrderDetail(id) {
      if (this.isActive == 0) {
        this.$router.push({ name: 'buyPage', query: { order_num: id } })
      } else if (this.isActive == 1) {
        this.$router.push({ name: 'sellPage', query: { order_num: id } })
      }
    },
    // 获取初始购买数据
    getTabelData() {
      this.tableLoading = true
      getPmmaList({ account: sessionStorage.getItem('account'), p: this.page.p, size: this.page.size }).then(res=> {
        // console.log(res)
        if (res.data.status == 200) {
          this.tableLoading = false
          this.tableData = res.data.data.res
          this.page.total = res.data.data.count;
        } else {
          this.$public.msg(res.data.msg, 'error', this)
          return
        }
      })
    },
    // 获取出售列表
    getSellData() {
      this.tableLoading = true
      getSellList({ account: sessionStorage.getItem('account'), p: this.page.p, size: this.page.size }).then(res=> {
        // console.log(res)
        if (res.data.status == 200) {
          this.tableLoading = false
          this.tableData = res.data.data.res
          this.page.total = res.data.data.count;
        } else {
          this.$public.msg(res.data.msg, 'error', this)
          return
        }
      })
    },
    changeActive(num) {
      this.isActive = num
      this.page.p = 1
      if (num == 0) { // 购买列表
        this.getTabelData()
      } else if (num == 1) {//出售列表
        this.getSellData()
      }
    }
  },
  components: {
      pagination,
  }
}
</script>

<style lang="less">
   @import '../../../assets/index.less';
   @import 'transactionDetail.less';
</style>


